.project-details-container {
    padding: 2rem;
    max-width: 900px;
    margin: 0 auto;
  }
  
  .project-header {
    margin-bottom: 3rem;
  }
  
  .project-header h1 {
    font-size: 2.5rem;
    font-weight: 600;
    color: #2d3748;
    margin-bottom: 1rem;
  }
  
  .project-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  
  .project-tags .tag {
    background-color: rgba(102, 126, 234, 0.1);
    padding: 0.4rem 0.8rem;
    border-radius: 2rem;
    font-size: 0.9rem;
    color: #667eea;
  }
  
  .project-details-container section {
    margin-bottom: 2.5rem;
  }
  
  .project-details-container h2 {
    font-size: 1.8rem;
    font-weight: 600;
    color: #2d3748;
    margin-bottom: 1rem;
    border-bottom: 2px solid #e2e8f0;
    padding-bottom: 0.5rem;
  }
  
  .project-details-container p {
    margin-bottom: 1rem;
    font-size: 1.1rem;
    line-height: 1.6;
    color: #4a5568;
  }
  
  .project-details-container ul {
    list-style-type: none;
    padding-left: 1.5rem;
  }
  
  .project-details-container li {
    margin-bottom: 0.8rem;
    position: relative;
    font-size: 1.1rem;
    line-height: 1.5;
    color: #4a5568;
  }
  
  .project-details-container li:before {
    content: "•";
    position: absolute;
    left: -1.5rem;
    color: #667eea;
    font-size: 1.2rem;
  }
  
  .links-container {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
  }
  
  .project-link {
    display: inline-block;
    padding: 0.7rem 1.4rem;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    text-decoration: none;
    border-radius: 0.5rem;
    font-weight: 500;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .project-link:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  @media (max-width: 768px) {
    .project-details-container {
      padding: 1rem;
    }
    
    .project-header h1 {
      font-size: 2rem;
    }
    
    .project-details-container h2 {
      font-size: 1.5rem;
    }
    
    .project-details-container p,
    .project-details-container li {
      font-size: 1rem;
    }
  }
